<template>
    <div class="ion-segment segment" :class="[modeClass,colorClass]">
        <slot></slot>
    </div>
</template>
<style lang="scss">
    @import './segment.scss';
    @import './segment.ios.scss';
    @import './segment.md.scss';
</style>
<script type="text/javascript">
  /**
   * @component Segment
   * @description
   *
   * ##  / Segment
   *
   * Segment, , . , Tab, .
   *
   * ### 
   *
   * SegmentSegmentButton, . `v-model`. , value, value.
   *
   * ### 
   *
   * 1. , this, recordChild()
   * 2. ,  onChildChange , value
   * 3. valueinputv-modal, , setChecked, value
   * 4. value
   *
   * ### 
   *
   * , . value, value, SegmentButtontext.
   *
   * ### `v-model`
   *
   * `v-model`, `value`, `v-model`, value`onChange`, , value, .
   *
   *
   * ### 
   *
   * ```
   * // 
   * import { Segment, SegmentButton } from 'vimo/components/segment'
   * // 
   * Vue.component(Segment.name, Segment)
   * Vue.component(SegmentButton.name, SegmentButton)
   * ```
   *
   * @props {String} color - 
   * @props {String} mode - 
   * @props {String} value - Segmentvalue, value
   *
   * @fires component:Segment#onChange
   * @demo https://dtfe.github.io/vimo-demo/#/segment
   *
   *
   * @usage
   * <Header>
   *    <Navbar>
   *        <Title></Title>
   *    </Navbar>
   *    <Toolbar>
   *        <!--content-->
   *        <Segment v-model="fruit" @onChange="onChangeHandler">
   *            <SegmentButton value="apple" @onSelect="onSelectHandler">Apple</SegmentButton>
   *            <SegmentButton value="orange" @onSelect="onSelectHandler">Orange</SegmentButton>
   *            <SegmentButton value="pear" @onSelect="onSelectHandler">Pear</SegmentButton>
   *            <SegmentButton value="disabled" :disabled="true" @onSelect="onSelectHandler">Disabled</SegmentButton>
   *        </Segment>
   *    </Toolbar>
   * </Header>
   *
   * */
  export default{
    name: 'Segment',
    props: {
      /**
       * value
       * */
      value: [String, Number],
      /**
       * colorprimarysecondarydangerlightdark
       * */
      color: [String],
      /**
       * mode  ios/window/android/we/alipay
       * */
      mode: {
        type: String,
        default () { return this.$config && this.$config.get('mode') || 'ios' }
      },
      disabled: [Boolean]
    },
    data () {
      return {
        // valuepropsvalue
        childComponents: [],
        timer: null,
        theValue: this.value
      }
    },
    watch: {
      value (value) {
        // 
        this.refreshChildState(value)
      }
    },
    computed: {
      // 
      modeClass () {
        return `segment-${this.mode}`
      },
      // 
      colorClass () {
        return this.color ? (`segment-${this.mode}-${this.color}`) : ''
      }
    },
    methods: {

      // ------ for children ------
      /**
       * , 
       * @param {Object} childComponent - (this)
       * @private
       * */
      recordChild (childComponent) {
        this.childComponents.push(childComponent)

        this.timer && window.clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          // 
          this.refreshChildState(this.value)
        }, 0)
      },

      /**
       * 
       * @param {string} value - 
       * @private
       * */
      onChildChange (value) {
        // 
        this.refreshChildState(value)
        this.$emit('input', value)
        /**
         * @event component:Segment#onChange
         * @description  onChangevalue
         * @property {string} value - 
         */
        this.$emit('onChange', value)
      },

      /**
       * 
       * @private
       * */
      refreshChildState (value) {
        this.childComponents.forEach((childComponent) => {
          if (!childComponent.isDisabled) {
            childComponent.setState(value)
          }
        })
      }
    }
  }
</script>



